<template>
  <div class="manageaddact">
    <el-form id='el_form_manageaddact' :model='form' label-width='100px'>
      <el-form-item label='活动名称'>
        <el-input v-model='form.name'></el-input>
      </el-form-item>
      <el-form-item label='活动类型'>
        <el-input v-model='form.type'></el-input>
      </el-form-item>
      <el-form-item label='信息时间'>
        <el-date-picker
        v-model="form.valid_date"
        label="活动信息有效期的截止时间"
        type="date"
        placeholder="活动信息有效期的截止时间"
        value-format="yyyy-MM-dd"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label='学校'>
        <el-input v-model='form.school'></el-input>
      </el-form-item>
      <el-form-item label='活动地点'>
        <el-input v-model='form.location'></el-input>
      </el-form-item>
      <el-form-item label='活动内容'>
        <el-input
        v-model='form.act_detail'
        autosize
        placeholder="请输入内容"
        type="textarea"
        :rows="2">
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button
        type="primary"
        @click="onSubmit"
        v-loading.fullscreen.lock="loadingfullscreen"
        >提交</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
    <div>{{ signupResponse }}</div>
  </div>
</template>

<script>
export default {
  name: 'ManageAddActivities',
  components:{},
  data () {
    return {
      form: {
        name:"",
        type :"",
        valid_date : "",
        school : "",
        taglist :[], // 
        location :'',
        act_detail:''
      },
      signupResponse:'',
      loadingfullscreen:false
    }
  },
  methods: {
    onSubmit() {
      this.loadingfullscreen=true;
      axios.post('http://127.0.0.1/manage/activities/publish',this.form).then(
        (response)=>{
          console.log(response.status);
          if (response.data['errcode']!=0){
              this.$message(response.data['errmsg']);
              this.loadingfullscreen=false;
            } else {
              this.$message(response.data['data']);
              this.form.name="";
              this.form.type ="";
              this.form.valid_date = "";
              this.form.school = "";
              this.form.taglist =[]; // 
              this.form.location ='';
              this.form.act_detail='';
              this.loadingfullscreen=false;
            }
          
        }).catch((error) => {
          this.$message('添加失败');
          this.loadingfullscreen=false;
        });
      console.log('submit!');
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.manageaddact {
  color: #42b983;
  width: 40em;
  margin: 0 auto;
  margin-top: 3em;
}

#el_form_manageaddact{
  margin-left: 10px;
}
</style>



